export default {
	data() {
		return {
			meetingList:[],
			flag:true,
			pageIndex:0,
			isOver:false
		}
	},
	onLoad() {
		this.getToken()
	},
	onReachBottom() {
		var that = this
		if(this.meetingList.length < this.pageIndex*5){
			this.isOver = true
		}
		console.log(this.meetingList.length)
		this.pageIndex++
		this.getmeetingList()
	},
	methods: {
		//获取token
		getToken(){
			var that = this
			uni.getStorage({
				key:'token',	
				success: function (res) {
				that.token = res.data.token
				that.getmeetingList()
				},
			})
		},
		//获取待开会列表
		getmeetingList(){
			var that = this;
			uni.request({
				url:'http://112.74.86.165:8082/meetingsMine',
				method:'GET',
				data:{
					pagination:this.pageIndex,
					size:'5'
				},
				header:{
					Authorization:that.token 
				},
				success(res) {
					console.log(res.data.data.list)
					that.meetingList = [...that.meetingList,...res.data.data.list]
					if(that.meetingList.length == 0){
						that.flag = false
					}
				}
			})
		},
		//撤销预约时间
		toUndo(id){
			var that = this;				
			console.log(id)
			console.log(that.token)
			uni.request({
				url:'http://112.74.86.165:8082/revocation',
				method:'PUT',
				data:{
					meetingId:id,
				},
				
				header:{
					Authorization:that.token,
					'content-type':"application/x-www-form-urlencoded"
				},
				success(res) {
					uni.navigateTo({
						url:'../../results/sUndo'
					})
				}
			})
			
		},
		//修改
		toUpdate(time,id,meetingName,meetingRoomId,name,phone){
			uni.navigateTo({
				url:'../update/update?id='+id+'&'+'meetingName='+meetingName+'&'+'meetingRoomId='+meetingRoomId+'&'+'phone='+phone+'&'+'name='+name+'&'+'time='+time
			})
		}
		
	}
}
